.neosidekick_appWrapper {
  display: grid;
  height: 100%;
  grid-template-columns: auto 40px;
  grid-template-rows: 100%;
}

.neosidekick_appWrapper--sidebar-open {
  grid-template-columns: auto 402px;
}

/* compability witn Neos 7 start */
.neosidekick_appWrapper--sidebar-open div[class*="style__primaryToolbar"] {
  width: calc(100% - 402px);
}
div[class*="style__rightSideBar"] {
  right: 40px;
}
.neosidekick_appWrapper--sidebar-open div[class*="style__rightSideBar"] {
  right: 402px;
}
/* compability witn Neos 7 end */

.neosidekick_sideBar {
  display: flex;
  flex-direction: column;
  background: #222;
  z-index: 800;
  border-left: 2px solid #141414;
}

.neosidekick_sideBar__title {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.neosidekick_sideBar__title-headline {
  padding: 0 40px 0 16px;
  height: 40px;
  line-height: 40px;
  margin: 0;
  font-size: 14px;
  font-weight: bold;
}

.neosidekick_sideBar__title-headline:not(.neosidekick_sideBar__title-headline--open) {
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(-90deg) translate(-8px,-97px);
  width: 230px;
  transition: all 0.2s;
}

.neosidekick_sideBar__frame {
  background: #141414;
  border: 0;
  flex-grow: 1;
  display: none;
}

.neosidekick_sideBar__frame--open {
  display: block;
}
